// eslint-disable

import React from 'react';
import PT from 'prop-types';
import { Link, graphql } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import { CardBox } from '../components/cardbox';

const createHistogram = (posts) => {
  return posts.reduce((hist, post) => {
    const postCategories = post.node.frontmatter.categories;
    for (const category of postCategories) {
      if (!(category in hist)) {
        hist[category] = 1;
      } else {
        hist[category] = hist[category] + 1;
      }
    }
    return hist;
  }, {});
}

class Categories extends React.PureComponent {
  render() {
    const { data, location } = this.props;
    const posts = data.allMarkdownRemark.edges;
    const histogram = createHistogram(posts);
    

    return (
      <Layout location={location} title="Categories" withVerticalPadding={false} withFooter>
        <CardBox>
          {Object.entries(histogram)
          .sort((a, b) => a[1] > b[1] ? -1 : 1)
          .map(([key, value]) => {
            return (
              <div key={key}>
                <div>{key} = {value}</div>
              </div>
            )
          })}
        </CardBox>
      </Layout>
    );
  }
}

Categories.propTypes = {
  data: PT.objectOf(PT.any).isRequired,
  location: PT.objectOf(PT.any).isRequired,
};


export default Categories;

export const pageQuery = graphql`
  query {
    allMarkdownRemark {
      edges {
        node {
          frontmatter {
            categories
          }
        }
      }
    }
  }
`;
